import React, { Component } from "react";
import { List, NavBar, Icon, Pagination, Button, Toast } from "antd-mobile";

const Item = List.Item;
const Brief = Item.Brief;

import { get } from "util/fetch";
import "./index.less";
import { setValue } from "@/redux/teacher-question";
import { connect } from "react-redux";

const routePath = "/api/exam_boot/teacher";
const imageHost = "https://jiguangdanci.easylesson.cn";
const locale = {
  prevText: "Prev",
  nextText: "Next"
};
const colorTheme = {
  0: "#e84118",
  1: "#00a8ff",
  2: "#fbc531",
  3: "#4cd137",
  4: "#487eb0",
  5: "#9c88ff"
};

class index extends Component {
  state = {
    questionList: [],
    current: 1
  };
  componentDidMount() {
    this.init();
  }
  init = () => {
    let list = this.props.teacherQuestion.list;
    let questionList = list.map(item => {
      item.user_answer = "";
      return item;
    });
    this.setState({
      questionList
    });
  };

  handleClick = id => {
    this.props.history.push(`/collection-word?collection_id=${id}`);

    log(id);
  };

  gotoLast = () => {
    this.props.history.go(-1);
  };
  handlePaginationChange = current => {
    if (current >= this.props.teacherQuestion.list.length) {
      Toast.info("已达终点", 2);
    }
    this.setState({
      current
    });
  };

  selectItem = value => {
    let { questionList, current } = this.state;
    questionList = questionList.map((item, index) => {
      if (index === current - 1) {
        item.user_answer = value;
      }
      return item;
    });
    this.setState({
      questionList
    });
    log(questionList);
  };
  render() {
    const question = this.state.questionList[this.state.current - 1];
    const questionOptionList = question && JSON.parse(question.optionList);
    const navBar = (
      <NavBar
        style={{
          backgroundColor: colorTheme[this.state.current % 5],
          color: "#fff"
        }}
        mode="light"
        onLeftClick={this.gotoLast}
        leftContent={<Icon type="left" />}
        rightContent={[<Icon key="1" type="ellipsis" />]}
      >
        Look and remember{" "}
      </NavBar>
    );
    const pagination = (
      <div className="pagination">
        <Pagination
          total={(this.props.teacherQuestion.list || []).length}
          current={this.state.current}
          locale={locale}
          onChange={this.handlePaginationChange}
        />
      </div>
    );
    const header = (
      <div className="header">
        <span>单项选择题</span>
        <span>
          {this.state.current}/{this.props.teacherQuestion.list.length}
        </span>
      </div>
    );
    const questionCell = (
      <div className="question">
        <div className="title">{question && question.content}</div>
        <div>
          {["A", "B", "C", "D"].map((item, index) => (
            <div
              key={item}
              onClick={() => {
                this.selectItem(item);
              }}
            >
              <span
                style={
                  item === (question && question.user_answer)
                    ? {
                        background: "green",
                        color: "white"
                      }
                    : {}
                }
                className="checkbox"
              >
                {item}
              </span>
              <span>{questionOptionList && questionOptionList[index]}</span>
            </div>
          ))}
        </div>
      </div>
    );
    const answerCell = (
      <div className="answer">
        <div>答案：{question && question.answer}</div>
        <div>解析：</div>
        <div>{question && question.analysis}</div>
      </div>
    );
    const content = (
      <div className="content">
        {header}
        {questionCell}
        {answerCell}
      </div>
    );
    const colorBar = (
      <div
        style={{
          height: "35px",
          width: "100%",
          marginBottom: "-5px",
          backgroundColor: colorTheme[this.state.current % 5]
        }}
      />
    );
    return (
      <div className="look-exam">
        {navBar}
        {/* {colorBar} */}

        {pagination}
        {content}
      </div>
    );
  }
}

const mapStateToProps = state => ({
  teacherQuestion: state.teacherQuestion
});
const mapDispatchToProps = dispatch => {
  return {
    setValue: value => {
      dispatch(setValue(value));
    }
  };
};
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(index);
